
.timeline .loadmore-text{font-size:14px;opacity:0.8;background-color:transparent;color:rgba(185,185,186,0.5);color:var(--faint, rgba(185,185,186,0.5))
}
.timeline .loadmore-error{font-size:14px;min-width:6em;text-align:center;padding:0 0.25em 0 0.25em;margin:0;color:#b9b9ba;color:var(--fg, #b9b9ba)
}
.new-status-notification{position:relative;margin-top:-1px;font-size:1.1em;border-width:1px 0 0 0;border-style:solid;border-color:var(--border, #222);padding:10px;z-index:1;background-color:#182230;background-color:var(--btn, #182230)
}



